<template>
  <div class="application-main">
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
    </div>
    <el-row :gutter="20">
      <el-col class="text-center" :lg="6" :xl="4">
        <div class="overview"><span>应用</span><i v-text="applicationsCount">1</i></div>
      </el-col>
      <el-col class="text-center" :lg="6" :xl="4">
        <div class="overview instance"><span>实例</span><i v-text="instancesCount">1</i></div>
      </el-col>
      <el-col :lg="6" :xl="4">
        <div class="overview config-up">
          <div>
            <p class="heading">实例正常</p>
            <p class="title has-text-danger" v-text="instancesCount - downCount"></p>
          </div>
        </div>
      </el-col>
      <el-col class="text-center" :lg="6" :xl="4">
        <div class="overview config-down">
          <!-- <div v-if="downCount === 0">
            <p class="heading">全部正常</p>
            <p class="title has-text-success">{{instancesCount}}</p>
          </div> -->
          <div>
            <p class="heading">实例停止/故障</p>
            <p class="title has-text-danger" v-text="downCount"></p>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-collapse class="mt-20" v-model="activeNames" accordion>
      <el-collapse-item v-for="(application, index) in instanceList" :key="index" :name="index">
        <template slot="title">
          <el-row class="header-row">
            <div class="application-header" :style="{background: (activeNames === index ? '#e6f4ff' : '#fff')}">{{application.name}}</div>
          </el-row>
        </template>
        <ul class="application-list">
          <li v-for="(instance, idx) in application.instances" :key="idx" @click="viewInstance(instance)">
            <el-row :gutter="10">
              <el-col :span="18" class="list-status">
                <sba-status :status="instance.statusInfo.status" :date="instance.statusTimestamp"/>
              <!-- </el-col> -->
              <!-- <el-col :span="14" class="list-status"> -->
                <a class="instance-link" target="_blank" v-text="instance.registration.serviceUrl || instance.registration.healthUrl"
                   :href="instance.registration.serviceUrl || instance.registration.healthUrl"
                   @click.stop></a>
                <span class="is-muted" v-text="instance.id"></span>
              </el-col>
              <el-col :span="6" class="list-version">{{instance.buildVersion}}</el-col>
            </el-row>
          </li>
        </ul>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex'
  import sbaStatus from './sba-status'

  export default {
    data() {
      return {
        activeNames: 0
      }
    },
    components: {
      sbaStatus
    },
    computed: {
      ...mapState({
        instanceList: state => state.application.instanceList
      }),
      applicationsCount() {
        return this.instanceList.length
      },
      instancesCount() {
        return this.instanceList.reduce((current, next) => current + next.instances.length, 0)
      },
      downCount() {
        return this.instanceList.reduce((current, next) => {
          return current + (next.instances.filter(instance => instance.statusInfo.status !== 'UP').length)
        }, 0)
      }
    },
    activated() {
      this.getApplication()
    },
    methods: {
      ...mapActions('application', ['getApplication']),
      viewInstance(instance) {
        this.$router.push({
          name: 'instance',
          params: {
            id: instance.id
          }
        })
      }
    }
  }
</script>

<style lang="scss">
  @import '../../assets/common';
  .application-main {
    [class^="fa fa-"] {
      margin-right: 0;
    }
    .up {
      margin: 10px 0;
      font-size: 12px;
    }
    .instance-link {
      color: $blue;
      margin-left: 20px;
      &:hover {
        text-decoration: underline;
      }
    }
    .is-muted {
      color: #6c7580;
      margin-left: 30px;
    }
    .application-header {
      line-height: 50px;
      padding-left: 40px;
      color: $dark-gray;
    }
    .el-collapse-item__wrap {
      border-bottom: none;
    }
    .el-collapse-item {
      border-bottom: 1px solid #ebeef5;
    }
    .el-collapse-item__content {
      padding-bottom: 0;
    }
    .el-collapse-item__header {
      line-height: normal;
      height: 50px;
      position: relative;
      .el-collapse-item__arrow {
        line-height: 50px;
        position: absolute;
        left: 20px;
        font-size: 12px;
        color: #333;
        z-index: 1;
        font-family: 'iconfont' !important;
        &::before {
          content: '\e619';
        }
      }
      .list-status {
        font-size: 13px;
        padding-top: 10px;
        line-height: 20px;
        > i {
          font-size: 16px;
          font-weight: bold;
          color: #23d160;
        }
      }
      .list-version {
        line-height: 50px;
      }
    }
    .application-list {
      > li {
        position: relative;
        cursor: pointer;
        border-bottom: 1px solid #ebeef5;
        &:last-child {
          border-bottom: none;
        }
        &:hover {
          background-color: #fafafa;
        }
        > .el-row {
          padding: 15px;
          line-height: 24px;
          font-size: 16px;
        }
      }
    }
    .overview {
      text-align: left;
      background: url('../../assets/images/applicaiton.png') 0 0 no-repeat;
      background-size: cover;
      border-radius: 4px;
      height: 120px;
      line-height: 120px;
      color: #fff;
      padding-left: 45px;
      font-size: 42px;
      position: relative;
      &.instance {
        background-image: url('../../assets/images/instance.png')
      }
      &.config-down {
        background-image: url('../../assets/images/confgi-down.png')
      }
      &.config-up {
        background-image: url('../../assets/images/config-start.png')
      }
      span, .heading {
        position: absolute;
        bottom: 20px;
        line-height: normal;
        font-size: 16px;
        font-weight: normal;
        right: 20px;
        &.status {
          color: #23d160;
        }
      }
    }
  }
</style>



