<template>
  <div class="application-status">
    <i class="application-status__icon fa" :class="[`application-status__icon--${status}`, `fa-${icon}`]"><span class="status-text" v-text="iconText"></span></i>
    <small v-if="date">
      <sba-time-ago :date="date"/>
    </small>
  </div>
</template>

<script>
  import sbaTimeAgo from './sba-time-ago'

  const icons = {
    'UP': 'check',
    'RESTRICTED': 'exclamation',
    'OUT_OF_SERVICE': 'ban',
    'DOWN': 'times-circle',
    'OFFLINE': 'minus-circle',
    'UNKNOWN': 'question-circle'
  }

  const iconsText = {
    'UP': '正常',
    'RESTRICTED': '限制',
    'OUT_OF_SERVICE': '停止',
    'DOWN': '故障',
    'OFFLINE': '离线',
    'UNKNOWN': '未知'
  }

  export default {
    components: {
      sbaTimeAgo
    },
    props: {
      status: {
        type: String,
        default: 'UNKNOWN'
      },
      date: {
        type: null,
        default: null
      }
    },
    computed: {
      icon() {
        return icons[this.status]
      },
      iconText() {
        return iconsText[this.status]
      }
    }
  }
</script>

<style lang="scss">
  .application-status {
    text-align: center;
    display: inline-block;
    .status-text {
      font-size: 14px;
      margin-left: 10px;
    }
    small {
      font-size: 14px;
      color: #40464d;
      border-right: 1px solid #40464d;
      padding-right: 20px;
    }
    &__icon {
      color: gray;
      margin: 0 auto;

      &--UP {
        color: #29ba9c;
        font-family: 'iconfont' !important;
        &::before {
          content: '\e61f'
        }
      }

      &--RESTRICTED {
        color: #ffdd57;
      }

      &--OUT_OF_SERVICE,
      &--DOWN {
        color: #fb7575;
        font-family: 'iconfont' !important;
        &::before {
          content: '\e61e';
        }
      }

      &--UNKNOWN,
      &--OFFLINE {
        color: #7a7a7a;
      }
    }
  }
</style>



